:root{
  --drkBG: #fcde67;
  --drkTXT: #030e12;
  --border: #5bccf6;
}

*{
  color: var(--drkTXT);
  margin: 0;
}

body{
  background-color: var(--drkBG)
}

h1{
  margin: 0;
  background-color: inherit;
  padding: 0 0 10px 7px;   
}

h2{
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 250%;
  border-right: 3px solid var(--border);
  min-width: 15%;
  color: var(--drkTXT);
}

h3{
  font-weight: bold;
  font-size: large;
  margin: 0 0 10px 0;
}

p{
  margin-right: 15px;
  padding: 30px;
  line-height: 1.75;
  text-indent: 40px;
}

a{
  font-family: 'Courier New', Courier, monospace;
  font-weight: bold;
  font-style: normal;
  text-decoration: underline;
}


#banner{
  background-image: url(http://shiel1dm.github.io/Portfolio/banner_img.jpg);
  height: 450px;
  background-size: cover;
  display: block;
}

.picture{
  border-radius: 50%;
  width: 325px;
  height: auto;
  
}

a{
  background-color: inherit;
  margin: 10px;
}

.navbar > a:hover{
  color: var(--drkBG);
}

#head{
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--border);
  height: 80px;
  font-size: 150%;
  color: white;
  padding-right: 7px;
  padding-left: 7px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

#page{
  /* not my initial plan but saw this in the solved HW.
  I like that it doesn't rely on setting padding and keeps the page at a percentage, regardless of screen-width, making it more mobile friendly imo*/
  max-width: 90%;
  margin-left: auto;
  margin-right: auto;
  font-size: 150%;
  margin-top: 50px;
}


#about{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: stretch;
  margin: 20px 0 25px 0;
  padding: 10px;
  
}

.bio{
  display: inherit;
  flex: 0 0 80%;
  flex-direction: column;
  font-size: x-large;
}

address > a{
  text-decoration: underline;
}

address > a:hover{
  color: white;
}

#work{
  margin: 25px 0;
  padding: 10px 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: stretch;
  justify-content: space-between;
  padding-left: 7px;
}

.container{
  flex: 0 0 80%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  
}

.box{
  border: 5px solid var(--border);
  background-color: var(--primary);
  display: flex;
  align-items: flex-end;
  flex: 0 0 25%;
  margin: 3px;
  height: 300px;
  width: 300px;
  border-radius: 10px;
  background-size: cover;
  text-decoration: none;
  font-family: 'Open Sans Condensed', sans-serif;
}

.box:hover > .info{
  box-shadow: 10px 5px 5px ;
  
}

.info{
  display: inherit;
  flex-direction: column;
  background-color: var(--border);
  margin: 0 0 20px 0;
  border: 3px solid var(--border);
  border-style: solid solid solid none;
  font-size: small;
  padding: 10px;
  font-size: 100%;
        
}

.box:first-child{
  min-height: 375px;
  flex-basis: 77%; 
}

.comic-call{
  background-image: url(http://shiel1dm.github.io/Portfolio/Spidey.jpg);
  font-family: 'Bangers', cursive;
}

.budgeteer{
  background-image: url(http://shiel1dm.github.io/Portfolio/Budgeteer.png);
}

.fitness{
  background-image: url(http://shiel1dm.github.io/Portfolio/Fitness.jpg);
}

.garden{
  background-image: url(http://shiel1dm.github.io/Portfolio/GardenBlog.png);
}

.warehouse{
  background-image: url(http://shiel1dm.github.io/Portfolio/Warehouse.png);
}

.e-manage{
  background-image: url(http://shiel1dm.github.io/Portfolio/Team.jpg);
}

#contact{
  display: block;
  text-align: center;
  margin: 100px 0 15px 0;
}

#contact > h2{
  border-right: none;
  border-bottom: 2px dashed white;
}

address{
  padding-top: 15px;
  font-size: 110%;
}

@media screen and (max-width: 1600px) {
  #about{
      flex-direction: column;
      align-items: center;
  }    
  #about > h2{
      border-right: 0;
      border-bottom: 5px solid var(--border);
      margin-bottom: 10px;
  }
}

@media screen and (max-width: 1280px) {
  #about, #work{
      flex-direction: column;
      align-items: center;
  }
  h2{
      border-right: 0;
      border-bottom: 5px solid var(--border);
      margin-bottom: 10px;
  }
}

@media screen and (max-width: 768px) {
  #head, #about, #work{
      flex-direction: column;
      align-items: center;
  }
  h2{
      border-right: 0;
      border-bottom: 5px solid var(--border);
      margin-bottom: 10px;
  }
}